import React from 'react'
import RandomLetters from '../RandomLetters'

class Context extends React.Component {state = {
  isActive: false
}

componentWillMount() {
  window.addEventListener('slidechange', this.onSlideChange, false)
}

componentWillUnmount() {
  window.removeEventListener('slidechange', this.onSlideChange, false)
}

onSlideChange = (event) => {
  this.setState({isActive: event.detail.index === this.props.index})
}
render() { return (
  <div className="scene-container">
    <div className="jumbotron">
      <div className="container">
        <div className="row grid-x grid-margin-x">
          <div className="col cell medium-12 animation-1">
            <h1><RandomLetters label="Context" start={this.state.isActive}/><span className="noscript-only">Context</span></h1>
            <p className="lead">Humanizing digital communication between people and businesses.</p>
            <div className="separator-40"/>
          </div>
        </div>
        <div className="row grid-x grid-margin-x">
          <p className="col cell medium-12 animation-2">Face-to-face communication is rapidly moving online. However, the quality of communication is not quite catching up in the online world, where people are represented as “accounts” and “profiles”.</p>
        </div>
        <div className="row grid-x grid-margin-x">
          <p className="col cell medium-12 animation-3">We recognize a personality in each individual online. Our technology gives accurate personality insights that elevate digital relationships between people and people, as well as between people and brands.</p>
        </div>
        <div className="row grid-x grid-margin-x">
          <p className="col cell medium-12 animation-4">Poor understanding of human nature results in frequent miscommunication between businesses and those they serve. People are not being offered genuine empathy, but one-size-fits-all marketing scripts out of context. Hence, low retention rates, no personalization of services, unhappy souls, and jeopardized reputation.</p>
        </div>
        <div className="row grid-x grid-margin-x">
          <p className="col cell medium-12 animation-4">Thus, by teaching technology to understand human psychology we’re teaching it to communicate with each personality with the respect and attention every one of us deserves in the digital world. Understanding starts with being understood. We make sure people love feeling understood, and their interactions online are more comfortable, more simple, and more enjoyable.</p>
        </div>
      </div>
    </div>
  </div>
)}}

export default Context



