import React, {Component} from 'react'
import MediaQuery from 'react-responsive'
import RandomLetters from '../RandomLetters'
import SnapHelper from '../SnapHelper'

const menuItems = ['Executive committee', 'Managing board', 'Research & Development', 'Technology & Data']

const people = [
	[{
		name: 'Sergey Yakubanets',
		column: 'large-4 animation-1',
		position: 'Co-Founder, Managing Partner',
                description: 'more than 20 years of experience in investments',
		image: require('../../images/photo-1.png'),
		ln: 'https://www.linkedin.com/in/sergey-yakubanets-a88028a0/'
	},{
		name: 'Maxim Katinov',
		column: 'large-4 animation-2',
		position: 'Co-Founder, Managing Partner',
                description: 'serial entrepreneur with over 15 years  experience managing small and medium sized enterprises in fintech, biotech and IT sectors,  from incubation to full commercial development',
		image: require('../../images/photo-2.png'),
		ln: 'https://www.linkedin.com/in/maxim-katinov-0a8702159/'

	},{
		name: 'Larisa Mararitsa',
		column: 'large-4 animation-3',
		position: 'Partner',
                description: 'Ph.D., Social Psychologist, researcher and specialist in the field of Social Interaction',
		image: require('../../images/photo-3.png'),
		ln: 'https://www.linkedin.com/in/larisa-mararitsa-b3595a159/'
	}],
	[{
		name: 'Andrey Goncharov',
		column: 'large-4 animation-1',
		position: 'Chief Product Officer',
                description: 'More than 10 years of experience in software development and product development',
		image: require('../../images/photo-goncharov.jpg'),
	}],
	[{
		name: 'Viktor Nozdrachev',
		column: 'large-4 animation-1',
		position: 'Chief Data Scientist',
        description: 'Ph.D., Mathematician',
		image: require('../../images/photo-nozdrachev.jpg'),
		ln: 'https://www.linkedin.com/in/victor-nozdrachev-034247159/'
	},{
		name: 'Sergey Titov',
		column: 'large-4 animation-2',
		position: 'Data Scientist',
        description: 'Ph.D. candidate in Psychology, Psychologist & Data Scientist',
		image: require('../../images/photo-titov.jpg'),
		ln: 'https://www.linkedin.com/in/titovs/'
	},{
		name: 'Pavel Novikov',
		column: 'large-4 animation-3',
		position: 'Data Scientist',
        description: 'MS in Data Science, Mathematician, programmer, more than 5 years of experience in R&D projects in Data Analysis and Social Networks',
		image: require('../../images/photo-novikov.jpg'),
		ln: 'https://www.linkedin.com/in/pavnovikov'
	},{
		name: 'Ekaterina Umanets',
		column: 'large-4 animation-4',
		position: 'Data Scientist',
        description: 'MS program in Data Science (National Research University Higher School of Economic)',
		image: require('../../images/photo-umanets.jpg'),
		ln: 'https://www.linkedin.com/in/ekaterina-umanets-330755161/'
	},{
		name: 'Yuri Rykov',
		column: 'large-4 animation-4',
		position: 'Sociologist',
        description: 'PhD, Research Fellow at Centre for Population Health Sciences LKC Medicine, Nanyang Technological University, Singapore. R&D experience in Online-communities, Social Networks and Social Capital Research',
		image: require('../../images/photo-rykov.jpg')
	},{
		name: 'Katerina Aleksandrova',
		column: 'large-4 animation-5',
		position: 'Psycholigist, Coach',
        description: 'Master\'s Degree in Social Psychology, Business & Life Coach',
		image: require('../../images/photo-aleksandrova.jpg'),
	}],
	[{
		name: 'Mikhail Danilov',
		column: 'large-4 animation-1',
		position: 'Chief Technical Officer',
        description: 'MSE, more than 10 years of experience in R&D projects in Engineering, Software Development and AI',
		image: require('../../images/photo-danilov.jpg'),
		ln: 'https://www.linkedin.com/in/mikhael-danilov-8363308a/'
	},{
		name: 'Aleksandr Marenkov',
		column: 'large-4 animation-4',
		position: 'Android Developer',
        description: 'MS in Economic Sciences (Moscow Power Engineering Institute)',
		image: require('../../images/photo-marenkov.jpg'),
	}]
]

class Team extends Component {
  state = {
    active: 0,
    isActive: false
  }

  componentWillMount() {
    window.addEventListener('slidechange', this.onSlideChange, false)
  }
  
  componentWillUnmount() {
    window.removeEventListener('slidechange', this.onSlideChange, false)
  }

  onSlideChange = (event) => {
    this.setState({isActive: event.detail.index === this.props.index})
    if (this.state.isActive) {
      this.setState({
        active: 0
      });
    }
  }

  snapHelper = new SnapHelper(this, '.body-team .item-descriptions', menuItems.length, 1024, '.body-team .active-slide .submenu');

  componentDidUpdate() {
    this.snapHelper.onComponentUpdate();
  }
  
  render() {
    return (
      <div className="scene-container">
        <div className="jumbotron">
          <div className="container">
            <div className="row grid-x grid-margin-x">
              <div className="col cell medium-12 animation-1">
                <h1><RandomLetters label="Team" start={this.state.isActive}/><span className="noscript-only">Team</span></h1>
              </div>
            </div>
		    <div className="row text-container grid-x grid-margin-x">
			  <p className="active col cell medium-12 animation-2">
				  We’re proud to have data scientists, psychologists, tech geniuses, sociologists, behavioral scientists, and talented entrepreneurs on board.
				  We merge various disciplines for the sake of innovations that will serve businesses and people worldwide.
			  </p>
		    </div>
            <div className="row grid-x grid-margin-x">
              <div className="col cell large-5 animation-2">
                <MediaQuery query="(max-device-width: 1024px)">
                  <div style={{width: '100%', overflowX: 'auto', WebkitOverflowScrolling: 'touch'}}>
                    <ul className="list-group list-group-flush no-bullet submenu">
                      {menuItems.map((item, index) => (
                        <li key={'menu-item-' + index}
                            onClick={() => this.setState({active: index})}
                            className={this.state.active == index ? 'list-group-item submenu-item active' : 'submenu-item list-group-item inactive'}>{item}.</li>
                      ))}
                    </ul>
                  </div>
                </MediaQuery>
                <MediaQuery query="(min-device-width: 1025px)">
                  <ul className="list-group list-group-flush no-bullet">
                    {menuItems.map((item, index) => (
                      <li key={'menu-item-' + index}
                          onClick={() => this.setState({active: index})}
                          className={this.state.active == index ? 'list-group-item active' : 'list-group-item inactive'}>{item}.</li>
                    ))}
                  </ul>
                </MediaQuery>
              </div>
              <div className="col cell large-7 item-descriptions animation-3">
                {people.map((group, index) => (

				  <div key={'people-group-' + index}
					   className={((this.state.active == index) ? 'active' : 'inactive') + ' item-description row grid-x grid-margin-x'} style={{'zIndex':((this.state.active == index) ? '10' : '0')}}>

					{group.map((employee, employeeIndex) => (

					  <div key={'employee-' + index + '-' + employeeIndex}
					       className={'employee col cell ' + employee.column}>
						  <div className="row image">
							<div className="thumbnail team-member-photo">
							  <img src={employee.image}/>
							</div>
						  </div>
						  <div className="row text">
							<h3>{employee.name}</h3>
							{
								employee.ln ? (
									<div>
										<p className="lead">{employee.position}</p>
										<p>
											<a href={employee.ln}>
											<img src={require("../../images/In-logo.png")}/>
											</a>
										</p>
										</div>
								) : (
									<p className="lead">{employee.position}</p>
								)
							}
							<p className="desc">{employee.description}</p>
						  </div>
					  </div>

					))}

                  </div>
                ))}
              </div>
            </div>
            <div className="paging">
              <div onClick={() => this.setState({active: Math.max(0, this.state.active - 1)})}>&lt;</div>
              <div onClick={() => this.setState({active: Math.min(3, this.state.active + 1)})}>&gt;</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Team



